
import { defineComponent, ref } from 'vue'

export default defineComponent({
  // 使用emit发送事件到父组件
  emits: ['foldChange'],
  setup(props, { emit }) {
    const isFold = ref(false)
    const handleFoldClick = () => {
      isFold.value = !isFold.value
      // 将事件名称和参数发送过去
      emit('foldChange', isFold.value)
    }
    return { isFold, handleFoldClick }
  }
})
